import {useRef, useState} from 'react';
import { styled } from '@stitches/react';
import {Trans, useTranslation} from "react-i18next";
import { HeartFilled } from '@ant-design/icons';
import AdjustLabel from "./AdjustLabel";
import {COLORS, FONT_NAME, FONT_SIZE, getI18nFont} from '../constants';

import useOnScreen from '../hooks/useOnScreen';
import {Properties} from "@stitches/react/types/css";

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

// top, right, bottom, left
// top, horizontal, bottom
// vertical, horizontal
const Layout = styled('div',{
  width: '100%',
  padding: isPortrait ? '5% 1% 15% 1%' : '5% 10% 5% 10%',
});

const Title = styled('div', {
  textAlign: 'center',
  color: COLORS.customBlue3,
  width: '100%',
  fontSize: FONT_SIZE.titleText2,
  margin: 0,
  fontWeight: '500',
});

// top, horizontal, bottom
const TitleLayout = styled('div', {
  width: '100%',
  padding: isPortrait ? '15% 10% 0%' : '5% 10% 0%',
});

const TitleStyle: Properties = {
  textAlign: 'center',
  color: COLORS.customBlue3,
  fontSize: FONT_SIZE.titleText2,
  margin: 0,
  fontWeight: '500',
};

const Table = styled('table', {
  width:'100%',
  marginTop: isPortrait ? '1rem' : '5rem',
  tableLayout: 'auto',
  borderSpacing: '1rem 0rem',
  borderCollapse: 'separate',

  '> tr > td:nth-child(1)': {
    width: '30%',
    color: COLORS.customBlue3,
    fontSize: FONT_SIZE.titleText2,
    textAlign: 'right',
    verticalAlign: 'baseline',
  },

  '> tr > td:nth-child(2)': {
    color: COLORS.customBlue2,
    fontSize: FONT_SIZE.bodyText1,
    verticalAlign: 'baseline',
  },
});

// top, right, bottom, left
const Contents = styled('p', {
  color: COLORS.customBlue2,
  width: '100%',
  fontSize: FONT_SIZE.bodyText1,
  paddingLeft: '1rem',
  paddingRight: '1rem',
  fontWeight: '300',
  lineHeight: 1.8,

  '> strong': {
    color: COLORS.customBlue3,
    fontSize: FONT_SIZE.titleText2,
  },
  '> span': {
    color: COLORS.customBlue2,
    fontSize: FONT_SIZE.bodyText2,
  },
});

type GreetingProps = {
};

const Greeting = ({ }: GreetingProps) => {
  const ref = useRef<HTMLSelectElement>(null);
  const { t } = useTranslation();

  const onScreen: boolean = useOnScreen<HTMLDivElement>(ref, '-125px');

  const greetingMessages = t("greetingMessage", {returnObjects: true}) as string[];

  const greetingMessage2 = (t("greetingMessage2", {returnObjects: true}) as string[]).map((value: string) => {
    return <Trans key={value} i18nKey={value} components={[<strong />, <span />]}>{value}</Trans>;
  });

  return (
    <section
      ref={ref}
      style={{
        height: '100vh',
        background: onScreen ? COLORS.customWhite : COLORS.customWhite,
        overflow: 'hidden',
        position: 'relative',
        transition: 'background 1s ease-in',
      }}
    >
      <TitleLayout>
      {/*  <AdjustLabel style={ TitleStyle } >{t('greetingTitle')}</AdjustLabel>*/}
        <Title style={{ fontFamily: getI18nFont(FONT_NAME.LATO) }} >{t('greetingTitle')}</Title>
      </TitleLayout>
      <Layout>
        {/*<Title style={{ fontFamily: getI18nFont(FONT_NAME.LATO) }} >{t('greetingTitle')}</Title>*/}
        <Table>
          <tr>
            <td>3</td>
            <td><Trans key={greetingMessages[0]} i18nKey={greetingMessages[0]} components={[<strong />, <span />]} /></td>
          </tr>
          <tr>
            <td>2</td>
            <td><Trans key={greetingMessages[1]} i18nKey={greetingMessages[1]} components={[<strong />, <span />]} /></td>
          </tr>
          <tr>
            <td>1</td>
            <td><Trans key={greetingMessages[2]} i18nKey={greetingMessages[2]} components={[<strong />, <span />]} /></td>
          </tr>
          <tr>
            <td>Start</td>
            <td><Trans key={greetingMessages[3]} i18nKey={greetingMessages[3]} components={[<strong />, <span />]} /></td>
          </tr>
        </Table>

        <div style={{ margin: '3rem 0rem', textAlign: 'center', color: COLORS.customBlue1 }}>
          <HeartFilled /> <HeartFilled /> <HeartFilled />
        </div>

        <Contents>
          <div style={{textAlign: 'center'}}>{ greetingMessage2 }</div>
        </Contents>
      </Layout>
    </section>
  );
};

export default Greeting;
