import { styled } from '@stitches/react';
import { useEffect, useState } from 'react';
import {Button, Divider, message, Modal} from 'antd';
import { MessageFilled, HeartFilled, LinkOutlined } from '@ant-design/icons';
import CopyToClipboard from 'react-copy-to-clipboard';
import { CommonsType } from '../configs';
import { Trans, useTranslation} from "react-i18next";
import {COLORS, FONT_NAME, FONT_SIZE, getI18nFont} from "../constants";
import i18next from "../lang/i18n";

import EndingImage from '../resources/ending.jpg';

declare global {
  interface Window {
    Kakao: any;
  }
}

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

// top, right, bottom, left
const Section = styled('section', {
  background: COLORS.customWhite,
  overflow: 'hidden',
  position: 'relative',
  padding: isPortrait ? '0% 0% 0% 0%' : '5% 20% 5% 20%',
});

const ImageLayout = styled('div', {
  /* flex-grow, flex-shrink, flex-basis */
  flex: '1 1 auto',
  display: 'flex',
  flexFlow: 'column',
  minHeight: '0%',
  animation: 'fadein 1s',
  padding: '10px',
});

const Image = styled('img', {
  objectFit: 'scale-down',
  overflow: 'hidden',
    flexGrow: '1',
  maxHeight: '100%',
});

const Contents = styled('p', {
  color: COLORS.customBlue2,
  width: '100%',
  fontSize: FONT_SIZE.smallText,
  fontWeight: '300',
  lineHeight: 1.8,
  textAlign: 'center',
  marginBottom: '3rem',
});

// top, right, bottom, left
const ButtonGroup = styled('div', {
  width: 'min-content',
  display: 'flex',
  flexDirection: 'column',
  margin: 'auto',
  paddingBottom: isPortrait ? '10%' : '5%',
});

const BankAccountButton = styled(Button, {
  background: COLORS.customBlue1,
  borderColor: COLORS.customBlue1,
  fontSize: FONT_SIZE.smallText,
  fontFamily: getI18nFont(FONT_NAME.LATO),
  color: COLORS.customWhite,
'&:hover': {
    backgroundColor: `${COLORS.customBlue2} !important`,
    borderColor: `${COLORS.customBlue2} !important`,
    color: `${COLORS.customWhite} !important`,
  },
  '&:focus': {
    backgroundColor: `${COLORS.customBlue2} !important`,
    borderColor: `${COLORS.customBlue2} !important`,
    color: `${COLORS.customWhite} !important`,
  },
  height: isPortrait ? '3rem' : '5rem',
});

const ShareButton = styled(Button, {
  background: COLORS.customBlue1,
  borderColor: COLORS.customBlue1,
  fontSize: FONT_SIZE.smallText,
  fontFamily: getI18nFont(FONT_NAME.LATO),
  color: COLORS.customWhite,
  '&:hover': {
    backgroundColor: `${COLORS.customBlue2} !important`,
    borderColor: `${COLORS.customBlue2} !important`,
    color: `${COLORS.customWhite} !important`,
  },
  '&:focus': {
    backgroundColor: `${COLORS.customBlue2} !important`,
    borderColor: `${COLORS.customBlue2} !important`,
    color: `${COLORS.customWhite} !important`,
  },
  height: isPortrait ? '3rem' : '5rem',
});

const LinkShareButton = styled(Button, {
  background: COLORS.customBlue1,
  borderColor: COLORS.customBlue1,
  fontSize: FONT_SIZE.smallText,
  fontFamily: getI18nFont(FONT_NAME.LATO),
  color: COLORS.customWhite,
  '&:hover': {
    backgroundColor: `${COLORS.customBlue2} !important`,
    borderColor: `${COLORS.customBlue2} !important`,
    color: `${COLORS.customWhite} !important`,
  },
  '&:focus': {
    backgroundColor: `${COLORS.customBlue2} !important`,
    borderColor: `${COLORS.customBlue2} !important`,
    color: `${COLORS.customWhite} !important`,
  },
  height: isPortrait ? '2.5rem' : '5rem',
  minWidth: '60vw'
});

const Description = styled('p', {
  fontSize: '1em',
  lineHeight: 1.75,
  opacity: 0.65,
  marginTop: '8px',
});


type ShareProps = {
  common: CommonsType;
};

const Share = ({ common }: ShareProps) => {
  const { t } = useTranslation();

  const [shareCount, setShareCount] = useState<number>(0);

  const [accountVisible, setAccountVisible] = useState<boolean>(false);

  if (!window.Kakao.isInitialized()) {
    window.Kakao.init(common.kakaoToken);
  }

  useEffect(() => {
    if (shareCount !== 0) {
      window.Kakao.Share.createDefaultButton({
        objectType: 'feed',
        container: '#sendKakao',
        content: {
          title: ` ${t('groom.shortName')} ❤${t('bride.shortName')} 결혼식에 초대합니다`,
          description: "아래의 '청첩장 열기' 버튼을 눌러주세요.🤵👰",
          imageUrl: common.kakaoImage,
          link: {
            mobileWebUrl: common.url,
            webUrl: common.url,
          },
        },
        buttons: [
          {
            title: '청첩장 열기',
            link: {
              mobileWebUrl: common.url,
              webUrl: common.url,
            },
          },
        ],
        installTalk: true,
      });
      setTimeout(() => {
        document.getElementById('sendKakao')?.click();
        message.success('카카오톡으로 청첩장을 공유합니다!');
      }, 100);
    }
  }, [common, shareCount]);

  return (
    <Section>
      <ImageLayout>
        <Image src={ EndingImage } alt="Wedding Invitation Title Picutre" />
      </ImageLayout>
      <Contents>#heestheoneforirish</Contents>

      <ButtonGroup>
        {i18next.language === 'ko' ?
          <BankAccountButton
            style={{ margin: 8, textAlign: 'center' }}
            icon={<HeartFilled />}
            size="large"
            onClick={() => setAccountVisible(true)}
          >마음 전하실 곳</BankAccountButton> : <></>
        }

        {i18next.language === 'ko' ?
          <ShareButton
            style={{ margin: 8, textAlign: 'center' }}
            icon={<MessageFilled />}
            id="sendKakao"
            size="large"
            onClick={() => setShareCount(shareCount + 1)}
          >
            카카오톡으로 공유하기
          </ShareButton> : <></>
        }

        <CopyToClipboard text={common.url}>
          <LinkShareButton
            style={{ margin: 8, textAlign: 'center' }}
            icon={<LinkOutlined />}
            size="large"
            onClick={() => message.success(t('linkCopied'))}
          >
            {t('copyLink')}
          </LinkShareButton>
        </CopyToClipboard>
      </ButtonGroup>
      <Modal
          title={<b>마음 전하실 곳(계좌번호)</b>}
          open={accountVisible}
          onOk={() => setAccountVisible(false)}
          onCancel={() => setAccountVisible(false)}
          cancelButtonProps={{ style: { display: 'none' } }}
          okButtonProps={{ style: { display: 'none' } }}
          footer={[<Description>계좌번호 클릭시, 붙여넣기 가능한 텍스트로 복사됩니다.</Description>]}
      >
        <div>
          <b>신랑측 | { t('groom.fatherName') }</b>
          <Divider type="vertical" />
          <CopyToClipboard text={ t('groom.fatherAccountNumber') || ''}>
            <Button
                type="text"
                style={{ padding: 0, margin: 0 }}
                onClick={() => message.success('계좌번호가 복사되었습니다.')}
            >
              { t('groom.fatherAccountNumber') || ''}
            </Button>
          </CopyToClipboard>
        </div>
        <div style={{ marginTop: 24, marginBottom: 24 }}>
          <b>신랑측 | { t('groom.motherName') }</b>
          <Divider type="vertical" />
          <CopyToClipboard text={ t('groom.motherAccountNumber') || ''}>
            <Button
                type="text"
                style={{ padding: 0, margin: 0 }}
                onClick={() => message.success('계좌번호가 복사되었습니다.')}
            >
              { t('groom.motherAccountNumber') || ''}
            </Button>
          </CopyToClipboard>
        </div>
        <div>
          <b>신랑측 | { t('groom.fullName') }</b>
          <Divider type="vertical" />
          <CopyToClipboard text={ t('groom.accountNumber') }>
            <Button
                type="text"
                style={{ padding: 0, margin: 0 }}
                onClick={() => message.success('계좌번호가 복사되었습니다.')}
            >
              { t('groom.accountNumber') }
            </Button>
          </CopyToClipboard>
        </div>
        <div style={{ marginTop: 24, marginBottom: 24 }}>
          <b>신부측 | { t('bride.fullName') }</b>
          <Divider type="vertical" />
          <CopyToClipboard text={ t('bride.accountNumber') }>
            <Button
                type="text"
                style={{ padding: 0, margin: 0 }}
                onClick={() => message.success('계좌번호가 복사되었습니다.')}
            >
              { t('bride.accountNumber') }
            </Button>
          </CopyToClipboard>
        </div>
      </Modal>
    </Section>
  );
};

export default Share;
